@import url(https://fonts.googleapis.com/css?family=Roboto:400,900&subset=latin,latin-ext,greek,cyrillic,greek-ext,vietnamese,cyrillic-ext);

body {
	 background: #000;
   color: #bbbbbb;
}

ul {
	list-style: none;
}

ul li {
	display: inline;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;
  -webkit-align-content: flex-end;
  align-content: flex-end;
}

.clockwall{
 /* PlaceHolder */
}

.clock {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 12px;
	/*border: 1px solid #333333;*/
	color: #FFFFFF;
  text-align: left;
  padding: 15px;
	min-width: 350px;
}

.time {
  font-size: 6em;
}

.minutes {
  padding-right: 5px;
}

.seconds {
  font-size: 0.7em;
}

.seconds sup {
  vertical-align: super;
}

.date {
	font-size: 2em;
}

.town {
  font-size: 2em;
	margin-bottom: 4px;
}

.timezone {
	font-weight: lighter;
}

.points {
	position: relative;
	-moz-animation: blink 1s ease infinite;
	-webkit-animation: blink 1s ease infinite;
}

@-webkit-keyframes blink {
	0% {opacity:1.0;}
	50% {opacity:0;}
	100% {opacity:1.0;}
}

@-moz-keyframes blink {
  0% {opacity:1.0;}
  50% {opacity:0;}
  100% {opacity:1.0;}
}

@keyframes blink {
  0% {opacity:1.0;}
  50% {opacity:0;}
  100% {opacity:1.0;}
}

#clock1, #clock6 {
	background: #05668D;
	color: #B3D1DC;
}

#clock2, #clock7 {
	background: #028090;
	color: #B2D9DD;
}

#clock3, #clock8 {
	background: #00A896;
	color: #B2E5DF;
}

#clock4, #clock9 {
	background: #02C39A;
	color: #164B4C;
}

#clock5, #clock10 {
	background: #F0F3BD;
	color: #5F5A57;
}
